@use 'helpers/variables' as v;
@use 'helpers/mixins' as m;

@include m.desktop {
    .card {
        display: flex;
        align-items: center;
        gap: 6rem;
    }
}

.marketing {
    color: v.$white;
    @include m.desktop {
        text-align: left;
        max-width: 50rem;
        h1 {
            font-size:4.5rem;
            line-height: 1;
        }
    }
}

.registration-info {
    padding: 0.2rem ;
    background-color: v.$blue;
    color: v.$white;
    margin-top: 4rem;
    margin-bottom: 3rem;
    border-radius: 1rem;
    span {
        font-weight: 700;
    }
}

.shadow {
    -webkit-box-shadow: 1px 10px 5px 0px rgba(0,0,0,0.2);
    -moz-box-shadow: 1px 10px 5px 0px rgba(0,0,0,0.2);
    box-shadow: 1px 10px 5px 0px rgba(0,0,0,0.2);
}

