@use 'helpers/variables' as v;
@use 'helpers/mixins' as m;

.registration-form {
    background-color: v.$white;
    display: flex;
    flex-direction: column;
    padding: 2rem;
    border-radius: 1rem;
    .button {
        color: v.$white;
        text-transform: uppercase;
        border: none;
        background-color: v.$green;
        margin-bottom: 0;
        width: 100%;
        cursor: pointer;
        background-image: none;
        &:hover {
            opacity: 75%;
        }
    }

}

.field {
    margin-bottom: 1rem;
    p {
        display: none;
        font-size: 1rem;
        font-style: italic;
        text-align: right;
        margin-top: 1rem;
    }
}

.notice {
    padding: 0 2.5rem;
    font-size: 1rem;
    line-height: 2rem;
    color: v.$grayishBlue;
    span {
        color: v.$red;
        font-weight: 700;
        cursor: pointer;
    }
}

.error {
    .message {
        display: block;
    }
    color: v.$red;
    input {
        border: 2px solid v.$red;
    }

    input::placeholder {
        background-image: url(../../images/icon-error.svg);
        background-repeat: no-repeat;
        background-position: center right 1rem;
        color: v.$red;
    }
}