@use 'helpers/variables' as v;
@use 'helpers/mixins' as m;

/* apply a natural box layout model to all elements, but allowing components to change */
html {
    box-sizing: border-box;
    font-size: 62.5%; // makes 1rem = 10px
  }
  *, *:before, *:after {
    box-sizing: inherit;
}

body {
  font-family: v.$fontFamily;
  font-size: 1.6rem;
  font-weight: 400;
  width: 100vw;
  height: 100vh;
  background-image: url(../../images/bg-intro-mobile.png);
  background-color: v.$red;
  line-height: 1.5;
}

h1 {
  margin: 0;
}


.container {
  height: 97vh;
  margin: 0 auto;
  max-width: 31rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;

  @include m.desktop{
      max-width: 105rem;
      gap: 5rem;
  }
}

input {
    width: 100%;
    padding: 1rem;
    margin-bottom: 2rem;
    border: 1px solid v.$grayishBlue;
    border-radius: 0.5rem;
    font-weight: 700;
    @include m.desktop {
        padding: 1.5rem;
    }
}